@media screen and (max-width:768px) {
  $flock-green : #0abe51;
  $text-color: rgba(0, 0, 0, 0.54);
  $header-background: #F0EFEA;
  $unpluggd-background: #fff;

  .webview_header {
    padding: 18px 11px 14px 21px;
    background: $header-background;
    color: $text-color;
    font-size: 15px;
    .back_icon {
      height: 16px;
      width: 16px;
      padding-right: 11px;
    }
    label {
      padding-left: 11px;
      font-size: 16px;
      position: relative;
      top: -2px;
    }
  }

  .webview_header_home {
    padding: 9px 11px 9px 21px;
    .back_icon {
      height: 30px;
      width: 30px;
      padding-right: 11px;
    }
    .width_auto {
      width: auto;
    }
    label {
      top: -10px;
    }
  }
  body {
    background: #fff;
  }

  .container {
    width: auto;
    color: $text-color;
    background: #fff;
  }
  .container img {
    width: 120px;
  }

  .container h2 {
    font-size: 18px;
    padding: 0 30px;
  }

  .title img {
    height: 25px;
    width: 24px;
  }

  .container .groupInfo {
    text-align: center;
  }

  .container .groupInfo .email {
    font-size: 16px;
    padding: 10px;
  }

  .container .groupInfo button {
    border-radius: 6px;
    font-size: 16px;
    padding: 10px 0;
    width: 160px;
  }

  .container .groupInfo span {
    font-size: 16px;
    text-align: center;
  }

  .container .login {
    left: 0;
    margin: 0 auto;
    position: absolute;
    right: 0;
    text-align: center;
    transform: translateY(-50%);
    text-align: center;
    top: 50%;
    width: 90%;
  }

  .container .login input {
    border: 1px solid #dedede;
    border-radius: 2px;
    font-size: 16px;
    margin-bottom: 20px;
    outline: none;
    padding: 10px;
    width: 78%;
  }

  .container .login button,
  .container .register_success button {
    background-color: #21c55f;
    border: 1px solid #21c55f;
    border-radius: 2px;
    color: #fff;
    font-size: 16px;
    outline: none;
    padding: 15px;
    width: 93%;
    margin-top: 35px;
  }

  .mobile {
    .mypin{
      margin-top: 0.27%;
      .main-heading {
        font-size: 22px;
        height: 25px;
        font-weight: 500;
        margin-bottom: 11.12%;
      }
      .pin {
        margin-top: 0.08%;
        color: $flock-green;
      }
      p {
        font-size: 16px;
        width: 65.94%;
        margin: auto;
        line-height: 1.2;
        padding: 0 5.56%;
      }
    }
  }

  //author: neha

  .unpluggd {
    background-color: $unpluggd-background;
  }

  .container {
    .items {
      padding: 0 14px;
      margin-top: 21px;
      div {
        display: inline-block;
        float: none;
      }
    }
    .module_img{
      img {
        width: 100%;
        height: auto;
      }
    }
    .header-banner {
      background-color: $unpluggd-background;
      img {
        width: 100%;
        height: 70px;
      }
      .height_auto {
        height: auto;
      }
    }
    .unplugged {
      margin-top: 0px;
      padding: 21px 16px;
      background-color: $unpluggd-background;
      h2 {
        font-family: ProximaNova-Bold;
        line-height: 1.3;
      }
    }
    .badge_key_wrapper {
      background-color: #fafafa;
      min-height: 430px;
    }
    .about_h2 {
      font-size: 16px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      text-align: center;
      color: #4a4a4a;
      margin-bottom: 19px;
    }
    .about_event {
      p {
        font-size: 16px;
        text-align: justify;
        margin: 0;
        &:last-child {
          margin: 14px 0;
        }
      }
    }
    .map-header {
      font-size: 16px;
      font-weight: 600;
      padding: 10px 15px;
      //min-height: 54px;
      font-style: normal;
      font-stretch: normal;
      text-align: left;
      color: #4a4a4a;
      margin: 0;
      background-color: $unpluggd-background;
      line-height: 1.1;
    }
    .map_wrapper {
      padding: 0;
      img {
        width:100%;
        // object-fit:contain;
        min-height: 490px;
      }
    }
    .sponsor_header {
      h2 {
        font-size: 14px;
        font-weight: 500;
        font-style: normal;
        font-stretch: normal;
        color: #0abe51;
        margin: 0;
        padding: 10px 16px;
        text-align: left;
      }
      .line {
        // width: 100%;
        opacity: 0.1;
        border: solid 1px rgba(0, 0, 0, 0.64);
      }
    }
    .divider {
      width: 90%;
      text-align: center;
      margin: 0 auto;
      font-size: 16px;
      font-family: ProximaNova-Bold;
      margin-top: 40px;
      margin-bottom: 20px;
      font-style: normal;
      font-stretch: normal;
      color: #6e6d6b;
    }

    .divider hr {
      margin-left:auto;
      margin-right:auto;
      width:25%;
    }

    .left {
      float:left;
    }

    .right {
      float:right;
    }

    .sponsor_li, .speaker_li {
      margin: 20px auto;
      img {
        //box-shadow:  0 0 2px 0 rgba(0,0,0,0.5);
        height: 80px;
        max-height: 100px;
        margin: 0 auto;
        display: block;
        max-width: 85%;
        width: auto;
        padding: 4px;
      }
      &:last-child{
        border-bottom: none;
        padding-bottom: 25px;
      }
    }

    .speaker_li {
      padding-bottom: 20px;
      border-bottom: solid 1px rgba(0, 0, 0, 0.14);
      width: 86%;
      margin: 28px auto;
      img {
        margin: 0 auto 16px;
      }
      p {
        font-size: 16px;
        margin: 0;
        font-weight: bold;
        font-style: normal;
        font-stretch: normal;
        text-align: center;
        color: #4a4a4a;
        line-height: 1;
      }
    }
    .info {
      background-color: #4a90e2;
    }
    .pin {
      background-color: #d565d0;
    }
    .schedules {
      background-color: #f5a623;
    }
    .exhibitors {
      background-color: #d44141;
    }
    .sponsors {
      background-color: #50e3c2;
    }
    .speakers {
      background-color: #4a4a4a;
    }
    .maps {
      background-color: #e09a10;
    }
    .website {
      background-color: #45a5f1;
    }
    .contacts {
      background-color: #6C00CC;
    }
  }
  .container {
    .header-banner {
      img {
        width: 100%;
        height: 70px;
      }
    }
    h2 {
      font-size: 16px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      text-align: center;
      color: #4a4a4a;
      margin-bottom: 19px;
    }
    .about_event {
      p {
        font-size: 16px;
        text-align: justify;
        margin: 0;
        &:last-child {
          margin: 14px 0;
        }
      }
    }
    .coming-soon {
      background: $unpluggd-background;
    //  display: table;
      width: 100%;
      text-align: center;
      h1 {
          left: 0;
          margin: 0 auto;
          position: absolute;
          right: 0;
          transform: translateY(-50%);
          top: 50%;
       // transform: translateY(42vh);
       // display: table-cell;
          font-size: 24px;
          font-weight: 600;
      }
    }
  }

  .unplugged .items {
    padding-left: 0;
    padding-right: 0;
    margin: auto;
    width: 385px;
    div {
      // (385-76*4)/8/3.844
      margin: 0 2.63%;
    }
  }
}

@media screen and (min-width:230px) and (max-width:319px) {
  .container .unplugged {
    padding-left: 0;
    padding-right: 0;
    .items {
      width: 230px;
      div {
        // (230-76*2)/4/2.297
        margin: 0 8.4%;
      }
    }
  }
}

@media screen and (min-width:320px) and (max-width:384px) {
  .unplugged .items {
    width: 268px;
    div {
      // (268-76*3)/6/2.672
      margin: 0 2.4%;
    }
  }
}

.container .sponsor_header .schedule_header {
  color: rgba(0, 0, 0, 0.54);
}

.topTabs {
  background-color: #F0EFEA;
  box-shadow: 0 1px 3px #f0efea;
}

.topTabs ul {
  list-style: none;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid #ccc;
}

.topTabs ul li {
  color: #fff;
  display: inline-block;
  font-size: 14px;
  opacity: 0.7;
  padding: 15px 0;
  text-align: center;
  width: 25%;
}

.topTabs ul li.active {
  border-bottom: 2px solid #4a4a4a;
  opacity: 1;
}

.topTabs ul li a {
  color: #4a4a4a;
  text-decoration: none;
}

.events {
  padding: 20px 0;
}

.events ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.events ul li {
  border-left: 4px solid #0abe51;
  margin-bottom: 20px;
  padding-left: 16px;
}

.events ul li p,
.summary p {
  font-size: 16px;
  margin: 0;
  padding-right: 16px;
}

.eventName{
  font-family: ProximaNova-Bold;
}

.events ul li p.time,
.summary p.time {
  color: #757575;
  font-size: 14px;
}

.events ul li p.tag,
.summary p.tag {
  background: #c7c095;
  border-radius: 20px;
  color: #ffffff;
  display: inline-block;
  font-size: 13px;
  padding: 5px 10px;
}

.summary {
  background: #f0efea;
  padding: 16px;
}

.event_details {
  color: #757575;
  padding: 16px;
}

.event_details ul {
  list-style: none;
  margin: 0 0 16px;
  padding: 0;
}

.event_details ul li {
  display: inline-block;
  line-height: 1.7;
  vertical-align: top;
}

.event_details ul li:first-child {
  width: 30%;
}

.event_details p {
  color: #757575;
  font-size: 14px;
  line-height: 1.7;
  margin: 0;
  padding: 0;
}

.container_accordion {
    width:100%;
}
.container_accordion div {
    width:100%;
    box-sizing: border-box;
    img {
      width: 16px;
      height: auto;
      float: right;
    }
}
.container_accordion .header_accordion {
    background-color: #F4F4F4;
    padding: 16px;
    cursor: pointer;
    font-weight: 700;
    border-bottom: 2px solid #ddd;
    box-sizing: inherit;
}
.container_accordion .content_accordion {
    display: none;
    padding : 5px;
    background: #FAFAFA;
    ul {
      padding: 0;
      margin: auto 1em;
      li {
        list-style: none;
        border-bottom: 2px solid #E9E9E9;
        padding: 1em 0;
        p {
          color: #4a4a4a;
          font-size: 26px;
          line-height: 1.5;
          font-size: 15px;
          margin: 0;
        }
        .post {
          font-family: ProximaNova-Semibold;
        }
      }
    }
}

.items div .long_text_icpc {
  height: auto !important;
  background: none !important;
  padding-top: 3px !important;
  color: #4a4a4a;
  position: relative;
  top: 1em;
}

.icpc_contacts {
  width: 40px !important;
  height: 33px!important;
  margin-top: 22px!important;
}
