//css for modal

@mixin transition ($time: .5s, $range: all, $ease: ease-out) {
  -moz-transition: $range $time $ease;
  -webkit-transition: $range $time $ease;
  -o-transition: $range $time $ease;
  transition: $range $time $ease;
}

@mixin transition-delay ($time: .4s) {
  -webkit-transition-delay: $time;
  -moz-transition-delay: $time;
  -o-transition-delay: $time;
  -transition-delay: $time;
}

@mixin border-radius($radius) {
  -moz-border-radius:$radius;
  -webkit-border-radius:$radius;
  border-radius: $radius;
}

@mixin gradient ($coler1: #fff, $coler2: #fff) {
  background: $coler1;
  background: -moz-linear-gradient($coler1, $coler2);
  background: -webkit-linear-gradient($coler1, $coler2);
  background: -o-linear-gradient($coler1, $coler2);
}

@mixin box-shadow($dims:0 0 10px, $color:#000) {
  box-shadow: $dims $color; // Opera, FFX4
  -moz-box-shadow:$dims $color; // FFX3.5
  -webkit-box-shadow:$dims $color; // Safari/Chrome/WebKit
  .ie7 { filter: e(%("progid:DXImageTransform.Microsoft.Shadow(color='%d', Direction=135, Strength=3)", $color)); }
}

@mixin inset($dims:1px 1px 1px, $color:#fff) {
  box-shadow: $dims $color; // Opera, FFX4
  -moz-box-shadow:$dims $color; // FFX3.5
  -webkit-box-shadow:$dims $color; // Safari/Chrome/WebKit
}


.button {
  margin: 40px auto;
  font-size: 72px;
  font-family: 'Yanone Kaffeesatz', Arial, sans-serif;
  text-decoration: none;
  text-shadow: 1px 1px 0px #fff;
  font-weight: 400;
  color: #666;
  border: 1px solid #ccc;
  cursor: pointer;
  padding: 20px 70px 30px;
  position: relative;
  top: 50px;
  background: #eee;
  width: 300px;
  display: block;
  text-align: center;
  @include inset;
  @include border-radius(5px);
  @include transition;
  &:hover{ color: #333; background: #eeffff; @include transition;}
}

.modalbg {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0,0,0,0);
  z-index: 99999;
  @include transition(2s);
  @include transition-delay(.2s);
  display: block;
  pointer-events: none;
  overflow-y: scroll;
  .dialog {
    width: 58%;
  max-width: 746px;
  position: relative;
  top: -1000px;
  margin: 10% auto;
  padding: 5px 20px 13px 20px;
  @include border-radius(7px);
  @include gradient;
  @include box-shadow;
  }
}

.modalbg:target {
  display: block;
  pointer-events: auto;
  background: rgba(4, 10 ,30, .8);
  @include transition();
  .dialog {
    top: 1.5em;
  @include transition(.8s);
  @include transition-delay;
  }
}

.close {
  background: #606061;
  color: #FFFFFF;
  line-height: 25px;
  position: absolute;
  right: -12px;
  text-align: center;
  top: -10px;
  width: 24px;
  text-decoration: none;
  font-weight: bold;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  border-radius: 12px;
  @include box-shadow;
  @include transition;
  @include transition-delay(.2s);
  &:hover { background: #00d9ff; @include transition; }
}

.dropdown {
  position: relative;
  display: inline-block;
  text-align: left;
  width: 132px;
}

.dropdown-text {
  cursor: pointer;
  position: absolute;
  text-indent: 10px;
  line-height: 38px;
  background-color: #eee;
  border: 1px solid #ccc;
  border-radius: 3px;
  box-shadow: 0 1px 0 rgba(255,255,255, .9) inset, 0 1px 3px rgba(0,0,0, .1);
  width: 100%;
  top: -26px;
  border-radius: 2px 0 0 2px;
  margin-left: 26px;
  width: 58%;
}

.dropdown-text:after {
  position: absolute;
  right: 6px;
  top: 15px;
  content: '';
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 5px 4px 0 4px;
  border-color: #555 transparent transparent transparent;
}

.dropdown-text,
.dropdown-content a {
  color: #333;
  text-shadow: 0 1px #fff;
}

@mixin dropdown-toggle() {
  font-size: 0;
  z-index: 1;
  cursor: pointer;
  position: absolute;
  top: 0;
  border: none;
  padding: 0;
  margin: 0 0 0 1px;
  background: transparent;
  text-indent: -10px;
  height: 34px;
  width: 100%;
}

.dropdown-toggle:focus {
  outline: 0;
}

.dropdown-content {
  -webkit-transition: all .25s ease;
  -moz-transition: all .25s ease;
  -ms-transition: all .25s ease;
  -o-transition: all .25s ease;
  transition: all .25s ease;
  list-style-type: none;
  position: absolute;
  top: 32px;
  padding: 0;
  margin: 0;
  opacity: 0;
  visibility:hidden;
  border-radius: 3px;
  text-indent: 10px;
  line-height: 32px;
  background-color: #eee;
  border: 1px solid #ccc;
  width: 140px;
}

.dropdown-content a {
  display: block;
}

.dropdown-content a:hover {
  background: #e8e8e8;
}


.dropdown-toggle:hover ~ .dropdown-text,
.dropdown-toggle:focus ~ .dropdown-text {
  background-color: #e8e8e8;
}

.dropdown-toggle:focus ~ .dropdown-text {
  box-shadow: 0 1px 3px rgba(0,0,0, .2) inset, 0 1px 0 rgba(255,255,255, 0.8);
  z-index: 2;
}

.dropdown-toggle:focus ~ .dropdown-text:after {
  border-width: 0 4px 5px 4px;
  border-color: transparent transparent #555 transparent;
}

.dropdown-content:hover,
.dropdown-toggle:focus ~ .dropdown-content {
  opacity: 1;
  visibility:visible;
  top: 42px;
}

.caret:after {
  position: absolute;
  right: 6px;
  top: 15px;
  content: '';
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 4px 0;
  border-color: #555 transparent transparent;
}
@import "mobile";
@media screen and (max-width:700px) {
  @import "desktop-responsive";
